<template>
		<div class="tradingRecord noTradingRecord">
			<div class="conentHeight" :class="{isOfferPay:isOfferPay}">
				<div class="clearfix topuphead">
					<a @click="$router.go(-1)" class="ago fl"><i class="icon iconfont icon-angle-left"></i></a>
					<router-link tag="a"  :to="{ name: 'home',params: { busId: busId }}" class="more fr" >
						<a >首页</a>
					</router-link>
				</div>
				<div class="offPayTop">
					<div class="OfferPay">
						<p class="title">扫一扫，向商家付款</p>
						<!-- <div class="cardNo">	
							<img :src="urlCardNo" >
						</div> -->
						<div class="OfferPayQR" >
							<img :src="urlQrcode" class="imgWidth">
							<p>{{cardNo}}</p>
							<p v-if="isOfferPay"  class="offerPayOther" @click="offerPayOther"><span>转借他人消费</span></p>
						</div>
					</div>
				</div>

				<div class="offerPayWindow" v-show="isOfferPayOther">
					<div class="offerPayWindowCom">
						<div v-if="islend">
							<p>请输入要转借的金额</p>
							<p>转借金额为零,默认转借时允许他人使用卡内所有消费金额</p>
							<div class=" clearfix">
								<label for="offerPayGive">¥</label>
								<input type="number" v-model='money' id="offerPayGive"  placeholder="请输入转借最高金额" name="">
							</div>
						</div>
						<!-- <div v-else>
							<p></p>
							<p class="copyCon">复制链接发送给好友,借储值金额给他消费</p>
							<textarea  readonly="readonly" id="shareId"  :value="shareUrl"  class="urlhide"></textarea>
							<div class="shareCon" v-show="isCopy" >赶紧发给好友吧！</div>
						</div> -->
						<div class="offerPayButo">
							<span @click="cancel">取消</span>
							<span  @click="offerPayGive">确定</span>
							<!-- <button  v-else @click="copyLink" class="btn" data-clipboard-target="#shareId">复制链接</button > -->
						</div>
					</div>
				</div>
				<!-- 
				<div class="overlayer" @touchmove.prevent  v-show="isCheckPopUp">
					<div class="offerPayWindow" v-show="noCopy">
						<div  class="offerPayWindowCom offerCopyCom">
							<div v-show="isCopy" class="nozhici">
								<p>您的浏览器不支持自动复制，请手动复制以下链接:</p>
								<p @click="selectText('shareUrl')" id="shareUrl">{{shareUrl}}</p>
								<textarea readonly="readonly" @click="selectText('shareUrl')" class="urlshow1" id="shareUrl" :value="shareUrl"></textarea>
							</div>
							<div class="offerPayButo">
								<p @click="sureCopy" class="sureunde">知道了</p>
							</div>
						</div>
					</div>
				</div> -->
			</div>
			<ClubFoot></ClubFoot>
		</div>
</template>
<script>
import Lib from '@/assets/js/Lib';
import Clipboard from 'clipboard';
import ClubFoot from "@/components/ClubFoot";
	export default{
		data() {
		    return {
		    	busId:0,
		    	// urlCardNo:'',
		    	urlQrcode:'',
		    	cardNo:'',
		    	intervalid:'',
		    	inputHide:true,
		    	time:120000,//刷新页面时间
		    	isOfferPayOther:false,
		    	isOfferPay:false,
		    	money:'',
		    	totlMoney:0,
		    	shareUrl:'123',
		    	islend:true,
		    	shareCon:'',
		    	isCopy:false,
		    	noCopy:false,
		    	isCheckPopUp:false,
		    	isLend:1
			}
		},
		components:{
		    ClubFoot
		},
		methods:{
			loadData:function(){
				var _this=this;
				_this.busId= _this.$route.params.busId;
				_this.url=window.location.href;
				// _this.urlCardNo=memberAPI.activeAPI.homeUrl+memberAPI.activeAPI.findPayJBarcodeCardNo_GET+"?busId="+_this.busId;
				var json={};
				json["busId"]=_this.busId;
				json["requestUrl"]=_this.url;
				Lib.M.ajax({
					'url':memberAPI.activeAPI.findMemberCardNO_POST,
	                'type': 'POST',
	                'data':{
	                   "json":json
	                },
	                'success':function (data){
	                	if(data.code==0){
	                		_this.cardNo=data.data.cardNo;
	                		_this.totlMoney=data.data.money;
	                		_this.urlQrcode=memberAPI.activeAPI.homeUrl+memberAPI.activeAPI.findPayQrcodeCardNo_GET+"?busId="+_this.busId;
	                		if(data.data.ctId==3){
	                			_this.isOfferPay=true;
	                		}
	                	}
	                	console.log('offpay',data)
	                	
	                }
				})
			},
			offerPayOther(){
				var _this=this;
				_this.islend=true;
				_this.isOfferPayOther=true;
				_this.money='';
			},
			cancel(){
				var _this=this;
				_this.isOfferPayOther=false;
				 _this.isCopy=false;
			},
			offerPayGive(){
				var _this=this,json={};
				if(_this.money==''){
					Lib.M.tipsMsg('请输入要转借的金额',2000);
		            return false;
				}
				if(_this.money>_this.totlMoney){
					Lib.M.tipsMsg('余额不足,请充值',2000);
		            return false;
				}
				if(_this.money==0){
					_this.money=_this.totlMoney;
				}
				json["busId"]=_this.busId;
				json["requestUrl"]=_this.url;
				json['money']=Number(_this.money);
				_this.isOfferPayOther=false;
				Lib.M.ajax({
					'url':memberAPI.activeAPI.memberLent_POST,
	                'type': 'POST',
	                'data':{
	                   "json":json
	                },
	                'success':function (data){
	                	if(data.code==0){

	                		if(data.data!=''||data.data!=null){
	                			var key;
	                			key=data.data;
	                			// _this.shareUrl=_this.url.split('#')[0]+'#/offerpayother/'+_this.busId+"?dataKey="+key+'&money='+_this.money;
	                			_this.$router.push({name:'offerpayother',query:{dataKey:key}})
	                		}
	                	}
	                	
	                	console.log('memberLent',data)
	                	
	                },
	                'warning':function(data){
	                	if(data.code==5006){
	                		Lib.M.tipsMsg(data.msg);
	                	}
	                }
				})
				
				_this.islend=false;
			},
			copyLink(){
				var _this=this;
				var clipboard = new Clipboard('.btn')
			    clipboard.on('success', e => {
			    	_this.isOfferPayOther=false;
			        Lib.M.tipsMsg('复制成功，赶紧发送给朋友吧！',3000);
			      // 释放内存
			      clipboard.destroy()
			    })
			    clipboard.on('error', e => {
			      _this.isOfferPayOther=false;
			     _this.noCopy=true;
			     _this.isCheckPopUp=true;
			      _this.isCopy=true;
			      // 释放内存
			      clipboard.destroy()
			    })
			},
			// closeOverlayer(){
			// 	var _this=this;
			// 	_this.isCheckPopUp=false;
			// },
			sureCopy(){
				var _this=this;
				_this.noCopy=false;
				_this.isCheckPopUp=false;
			},
			selectText(element) {
			     var shareUrl=document.getElementById("shareUrl");
	            var shareUrlValue=shareUrl.value,startIndex=0;
	            var valueLength = shareUrlValue.length;
			    if (shareUrl.setSelectionRange) {
	                shareUrl.setSelectionRange(startIndex, valueLength);
	                alert(111)
	            } else if (shareUrl.createTextRange) {
	                var range = shareUrl.createTextRange();
	                 alert(222)
	                range.collapse(true);
	                range.moveStart('character', startIndex);
	                range.moveEnd('character', valueLength - startIndex);
	                range.select();
	            }
	            shareUrl.focus();
	            try{
				    if(document.execCommand('copy', false, null)){
				       Lib.M.tipsMsg('复制成功！',3000);
				    } else{
				        Lib.M.tipsMsg('复制失败！',3000);
				    }
				} catch(err){
				    Lib.M.tipsMsg(err,3000);
				}
	            
			}
		},
	    created:function(){
	    	var _this=this;
	    	_this.loadData();
	    	_this.intervalid=setInterval(function(){
	    		_this.loadData();
	    	},_this.time)
	        
	    },
	    beforeDestroy () {
	      clearInterval(this.intervalid)
	    }
	}
</script>
<style scope>
	/*.urlshow{
		opacity: 1;
		z-index: 999;
	}
	.urlhide{
		opacity: 0;
	}*/
	.shareCon{
		font-size: 0.3rem;
		margin-bottom: 1rem;
	}
	.bgColor{
		background: #ff;
	}
</style>


