<template>
	<div class="tradingRecord">
		<div class="clearfix recommendTitleHead topuphead">
			<a @click="$router.go(-1)" class="ago fl"><i class="icon iconfont icon-angle-left"></i></a>
			<a class="active-bar" :style="styleObject"></a>
			<div class="fl" :class='{isActive:1===isMenu}' @click="clickMenu(1)">会员消息</div>
			<div class="fl" :class='{isActive:2===isMenu}' @click="clickMenu(2)">系统消息</div>
		</div>
		<div v-if="hasMessage" class="hasMessage">
			<!-- 新消息 -->
			<div v-if="huiyuan">
				<div class="newMessage">
					<div class="mymessage" v-for="(item,index) in noReadnoticeUsersData">
						<p class="clearfix"><span class="fl">{{messageTitle}}</span><span class="fr" @click="Spanclick(index+1)">{{isShow==(index+1)?'点击收起':'点击展开'}}</span></p>
						<div class="mymessageCom clearfix" >
							<h3 :class='isNewShow==(index+1)?"":"onlyH3"'>{{item.title}}</h3>
							<div v-show="isNewShow==(index+1)"  v-html="item.content"></div>
						</div>
					</div>
				</div>
				<!-- 已读消息 -->
				<div class="readMessage">
					<h3>{{readMessages}}</h3>
					<div class="mymessage" v-for="(item,index) in readnoticeUsersData">
						<p class="clearfix"><span class="fl">{{messageTitle}}</span><span class="fr" @click="Spanclick1(index+1)">{{isShow==(index+1)?'点击收起':'点击展开'}}</span></p>
						<div class="mymessageCom clearfix" >
							<h3 :class='isShow==(index+1)?"":"onlyH3"'>{{item.title}}</h3>
							<div v-show="isShow==(index+1)"  v-html="item.content"></div>
						</div>
					</div>
				</div>
			</div>
			<!-- 系统消息 -->
			<div v-else>
				<div class="newMessage">
					<div class="mymessage" v-for="(item,index) in noReadsystemNoticeSData">
						<p class="clearfix"><span class="fl">{{messageTitle}}</span></p>
						<div class="mymessageCom clearfix" >
							<h3>{{item.createDate}}</h3>
							<div  v-html="item.describes"></div>
						</div>
					</div>
				</div>
				<!-- 已读消息 -->
				<div class="readMessage">
					<h3>{{readMessages}}</h3>
					<div class="mymessage" v-for="(item,index) in readsystemNoticeSData">
						<p class="clearfix"><span class="fl">{{messageTitle}}</span></p>
						<div class="mymessageCom clearfix" >
							<h3 >{{item.createDate}}</h3>
							<div  v-html="item.describes"></div>
						</div>
					</div>
				</div>
			</div>
			
		</div>
		<div v-else>
			<div class="nomessage">暂无新消息</div>
		</div>
	</div>

</template>
<script>
	import Lib from '@/assets/js/Lib';
	export default{
		data() {
		    return {
		    	showTitle:"点击收起",
		    	messageTitle:'多粉会员通知',
		    	readMessages:'已读消息',
			    isShow: 0,
			    isNewShow:0,
			    isMenu:1,
			    hasMessage:true,
			    huiyuan:true,
			    busId:0,
			    url:'',
			    styleObject: {
				    width: '100%',
				    transform: 'translateX(0px)'
				},
			    noReadnoticeUsersData:[],
			    readnoticeUsersData:[],
			    noReadsystemNoticeSData:[],
			    readsystemNoticeSData:[],
			    menberUrl:'',
			    SystemUrl:''
			}
		},
		methods:{
			loadData:function(){
				var _this=this;
				_this.busId= _this.$route.params.busId;
				_this.url=window.location.href;
				var json={};
				json["busId"]=_this.busId;
				json["requestUrl"]=_this.url;
				Lib.M.ajax({
					'url':memberAPI.activeAPI.findMemberNotice_POST,
	                'type': 'POST',
	                'data':{
	                   "json":json
	                },
	                'success':function (data){
	                	console.log('Equities',data);
	                	if(data.code==0){
	                		_this.noReadnoticeUsersData=data.data.noReadnoticeUsers;
	                		_this.readnoticeUsersData=data.data.readnoticeUsers;
	                		_this.noReadsystemNoticeSData=data.data.noReadsystemNoticeS;
	                		_this.readsystemNoticeSData=data.data.readsystemNoticeS;
	                		_this.hasMessage=true;
	                		for(let i=0;i<_this.noReadsystemNoticeSData.length;i++){
		            			_this.noReadsystemNoticeSData[i].createDate=Lib.M.formatTime(_this.noReadsystemNoticeSData[i].createDate);
		            			console.log(_this.noReadsystemNoticeSData[i].createDate);
		            		}
		            		for(let i=0;i<_this.readsystemNoticeSData.length;i++){
		            			_this.readsystemNoticeSData[i].createDate=Lib.M.formatTime(_this.readsystemNoticeSData[i].createDate);
		            		}
	                	}
	                },
	                'error':function(err){
	                	Lib.M.tipsMsg(err.msg);
	                }
				})
			},
			clickMenu(pos){
				var _this=this;
				_this.isMenu=pos;
				let ind;
				if(pos==1){
					ind=0;
					_this.messageTitle='多粉会员通知';
					_this.huiyuan=true;
				}else{
					ind=pos;
					_this.messageTitle="系统消息";
					_this.huiyuan=false;
				}
				_this.styleObject.transform='translateX('+parseInt(ind)*parseInt(_this.styleObject.width)+'%)';
			},
			Spanclick(cur){
				if(this.isNewShow==cur){
					this.isNewShow=false;
				}else{
					this.isNewShow=cur;
				}
			},
			Spanclick1(cur){
				if(this.isShow==cur){
					this.isShow=false;
				}else{
					this.isShow=cur;
				}
			}
		},
		created:function(){
            this.loadData();

        }
	}
</script>
<style scope>
	
</style>


