<template>
		<div class="tradingRecord qrcode">
			<div class="clearfix topuphead">
				<a @click="$router.go(-1)" class="ago fl"><i class="icon iconfont icon-angle-left"></i></a>
				<router-link tag="a"  :to="{name:'home',params: { busId: busId }}" class="more fr" >
					<a >首页</a>
				</router-link>
			</div>
			<div class="OfferPay">
				<p v-if="isTuijian">把二维码发给好友，让好友一起加入会员行列吧！</p>
				<p v-else>您的好友给您推荐一张会员卡，赶紧领取吧!</p>
			</div>
			<div class="offerPayCom">
				<div class="OfferPayQR">
					<img :src="urlCardNo"  class="cardImg">
				</div>
				<p v-if="isTuijian">转发此页面给好友，加入会员行列</p>
				<p v-else>识别图中二维码领取会员卡</p>
				<!-- <p class="qrcodeButton">
					<textarea  readonly="readonly" id="shareId"  :value="shareUrl"  class="urlhide"></textarea>
					<button   @click="copyLink" class="btn" data-clipboard-target="#shareId">复制链接</button >
				</p> -->
			</div>
			<!-- <div class="overlayer" @touchmove.prevent  v-show="noCopy">
				<div class="offerPayWindow">
					<div  class="offerPayWindowCom offerCopyCom">
						<div v-show="isCopy" class="nozhici">
							<p>您的浏览器不支持自动复制，请手动复制以下链接:</p>
							<p @click="selectText('shareUrl')" id="shareUrl">{{shareUrl}}</p>
							<textarea readonly="readonly" @click="selectText('shareUrl')" class="urlshow" id="shareUrl" :value="shareUrl"></textarea>
						</div>
						<div class="offerPayButo">
							<p @click="sureCopy" class="sureunde">知道了</p>
						</div>
					</div>
				</div>
			</div> -->
			
		</div>
</template>
<script>
import Lib from '@/assets/js/Lib';
import axios from 'axios';
import Clipboard from 'clipboard';
	export default{
		data() {
		    return {
		    	busId:0,
		    	url:'',
		    	currUrl:'',
		    	urlCardNo:'',
		    	wxshareData:[],
		    	isCopy:false,
		    	noCopy:false,
		    	shareUrl:'',
		    	tuijianCode:'',
		    	isTuijian:true,
		    	time:'',
		    	share:{
		    		urlShare:'',
			    	shareTitle:'您的好友给您推荐一张会员卡，赶紧领取吧!',
			    	shareDesc:'识别图中二维码领取会员卡',
			    	shareImg:'http://wx.qlogo.cn/mmopen/ib5Ef9ImF1HN9hPsiaBlibGe5iaQDGFtJ4J33IicwtlB8YxSWzfLrmBNqjsdqOUM3MFtJpuZRdpNhwIqguw0D5ibOhxaFKBlsP20De/0'
		    	}
			}
		},
		methods:{
			loadData:function(){
				var _this=this,json={},ddd={};
				// debugger
				_this.currUrl=window.location.href;
				_this.busId= _this.$route.params.busId;
				_this.tuijianCode= _this.$route.query.tuijianCode;
				json["busId"]=_this.busId;
				json["requestUrl"]=_this.currUrl;
				json["wxshareUrl"]=_this.currUrl;
				json["systemCode"]=_this.tuijianCode;
				_this.share.urlShare=_this.currUrl;
				_this.urlCardNo=memberAPI.activeAPI.homeUrl+memberAPI.activeAPI.tuijianQRcode_GET+"?busId="+_this.busId+"&systemCode="+_this.tuijianCode;
				// _this.isfish=false;
				
				Lib.M.ajax({
			      	'url': memberAPI.activeAPI.judgeTuijian_POST,
			      	'type': 'POST',
			      	'data': {
			        	"json": json
			      	},
      				'success': function(data) {
      					if(data.code==0){
      						_this.isTuijian=data.data.tuijianCode;
      						_this.isTuijian=(_this.isTuijian==0)?true:false;
      					}
      					Lib.M.ajax({
					      	'url': memberAPI.activeAPI.wxshareCard_POST,
					      	'type': 'POST',
					      	'data': {
					        	"json": json
					      	},
		      				'success': function(data) {
		      					_this.wxshareData=data.data;
		      				},
		      				'error':function(data){
		      					// alert(data)
		      				}
		      			})
      					
      				}
      			})
				
			},
			copyLink(){
				var _this=this;
				var clipboard = new Clipboard('.btn')
			    clipboard.on('success', e => {
			    	_this.isOfferPayOther=false;
			          Lib.M.tipsMsg('复制成功，赶紧发送给朋友吧！',3000);
			      // 释放内存
			      clipboard.destroy()
			    })
			    clipboard.on('error', e => {
			      _this.isOfferPayOther=false;
			     _this.noCopy=true;
			      _this.isCopy=true;
			      // 释放内存
			      clipboard.destroy()
			    })
			},
			sureCopy(){
				var _this=this;
				_this.noCopy=false;
			},
			selectText() {
			     var NumClip=document.getElementById("shareUrl");
	            var NValue=NumClip.value,startIndex=0;
	            console.log(NValue)
	            var valueLength = NValue.length;
			    if (NumClip.setSelectionRange) {
	                NumClip.setSelectionRange(startIndex, valueLength);
	            } else if (NumClip.createTextRange) {
	                var range = NumClip.createTextRange();
	                range.collapse(true);
	                range.moveStart('character', startIndex);
	                range.moveEnd('character', valueLength - startIndex);
	                range.select();
	            }
	            NumClip.focus();
	            try{
				    if(document.execCommand('copy', false, null)){
				       Lib.M.tipsMsg('复制成功！',3000);
				    } else{
				        Lib.M.tipsMsg('复制失败！',3000);
				    }
				} catch(err){
				    Lib.M.tipsMsg(err,3000);
				}
			}
		},
	    mounted:function(){
	    	var isfirst=window.localStorage.getItem('first');
	    	if(isfirst==0){
	    		location.reload()
	    		window.localStorage.setItem('first',1)
	    	}
	    	var _this=this;
	    	_this.loadData()
		},
		destroyed(){
			window.localStorage.setItem('first',0)
		},
		watch:{
			wxshareData(newValue, oldValue){
				var _this=this;
				// alert(JSON.stringify(newValue))
				// alert(JSON.stringify(_this.share))
				// alert(newValue.signature);
				wx.config({
		            debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
		            appId: newValue.appid, // 必填，公众号的唯一标识
		            timestamp: newValue.timestamp, // 必填，生成签名的时间戳
		            nonceStr: newValue.nonce_str, // 必填，生成签名的随机串
		            signature: newValue.signature, // 必填，签名，见附录1
		            jsApiList: ["onMenuShareAppMessage", 'onMenuShareTimeline'] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
		        });
				wx.ready(function() {
		            //分享给朋友
		            wx.onMenuShareAppMessage({
			            title: _this.share.shareTitle, // 分享标题
			            desc: _this.share.shareDesc, // 分享描述
			            link: _this.share.urlShare, // 分享链接
			            imgUrl: _this.share.shareImg, // 分享图标
			            success: function() {
			                // 用户确认分享后执行的回调函数
			                Lib.M.tipsMsg('分享成功')
			            },
			            cancel: function() {
			                // 用户取消分享后执行的回调函数
			                 Lib.M.tipsMsg('取消分享')
			            },
			            error: function(res) {
			                // alert(res)
			            }
		            });
		            //分享到朋友圈
		            wx.onMenuShareTimeline({
		             	title: _this.share.shareTitle, // 分享标题
		             	desc: _this.share.shareDesc, // 分享描述
		              	link: _this.share.urlShare, // 分享链接
		              	imgUrl: _this.share.shareImg, // 分享图标
		              	success: function() {
		                // 用户确认分享后执行的回调函数
		              	},
		              	cancel: function() {
		                // 用户取消分享后执行的回调函数
		              	}
		            });
		        });
			}
		}
		
	}
</script>
<style scope>

</style>


