<template>
		<div class="tradingRecord qrcode">
			<div style="height:1px"></div>
			<div class="clearfix topuphead">
				<a @click="$router.go(-1)" class="ago fl"><i class="icon iconfont icon-angle-left"></i></a>
				<router-link tag="a"  :to="{name:'home',params: { busId: busId }}" class="more fr" >
					<a >首页</a>
				</router-link>
			</div>
			<div class="OfferPay">
				<p v-if="isLend">把二维码发送给好友,好友可借用该金额消费</p>
				<p v-else>你的好友给您借款,请扫码进行消费</p>
			</div>
			<div class="offerPayCom">
				<div class="OfferPayQR">
					<img :src="urlCardPay" width="88%">
				</div>
				<p v-if="isLend">转发此页面给好友,借储值金额给他消费</p>
				<p v-else>扫一扫,向商家付款</p>
			</div>
		</div>
</template>
<script>
import Lib from '@/assets/js/Lib';
	export default{
		data() {
		    return {
		    	busId:0,
		    	url:'',
		    	urlCardPay:'',
		    	currUrl:'',
		    	dataKey:'',
		    	wxshareData:[],
		    	isLend:1,
		    	shareCon:{
		    		shareUrl:'',
			    	payTitle:'你的好友借款给您,请扫码进行消费!',
			    	payDesc:'扫一扫,向商家付款',
			    	payImg:'http://wx.qlogo.cn/mmopen/ib5Ef9ImF1HN9hPsiaBlibGe5iaQDGFtJ4J33IicwtlB8YxSWzfLrmBNqjsdqOUM3MFtJpuZRdpNhwIqguw0D5ibOhxaFKBlsP20De/0'
		    	}
			}
		},
		methods:{
			loadData:function(){
				var _this=this,json={};
				_this.busId= _this.$route.params.busId;
				_this.dataKey= _this.$route.query.dataKey;
				_this.currUrl=window.location.href;
				_this.shareCon.shareUrl=_this.currUrl;
				json["busId"]=_this.busId;
				json["requestUrl"]=_this.currUrl;
				json["memberLentKey"]=_this.dataKey;
				json["wxshareUrl"]=_this.currUrl;
				Lib.M.ajax({
					'url':memberAPI.activeAPI.judgememberLent_POST,
	                'type': 'POST',
	                'data':{
	                   "json":json
	                },
	                'success':function (data){
	                	_this.isLend=data.data.lentCode;
      					_this.isLend=(_this.isLend==0)?true:false;
	                	if(data.code==0){
	                		_this.urlCardPay=memberAPI.activeAPI.homeUrl+memberAPI.activeAPI.memberLentImage_GET+"?busId="+_this.busId+"&memberLentKey="+_this.dataKey;
	                		Lib.M.ajax({
						      	'url': memberAPI.activeAPI.wxshareCard_POST,
						      	'type': 'POST',
						      	'data': {
						        	"json": json
						      	},
			      				'success': function(data) {
			      					_this.wxshareData=data.data;
			      				}
			      			})
	                	}
	                	
	                }
				})
			}
		},
	    created:function(){
	    	var isfirst=window.localStorage.getItem('first');
	    	if(isfirst==0){
	    		location.reload()
	    		window.localStorage.setItem('first',1)
	    	}
	    	var _this=this;
	    	_this.loadData()
		},
		destroyed(){
			window.localStorage.setItem('first',0)
		},
	    watch:{
			wxshareData(newValue, oldValue){
				var _this=this;
				wx.config({
		            debug: true, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
		            appId: newValue.appid, // 必填，公众号的唯一标识
		            timestamp: newValue.timestamp, // 必填，生成签名的时间戳
		            nonceStr: newValue.nonce_str, // 必填，生成签名的随机串
		            signature: newValue.signature, // 必填，签名，见附录1
		            jsApiList: ["onMenuShareAppMessage", 'onMenuShareTimeline'] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
		        });
				wx.ready(function() {
		            //分享给朋友
		            wx.onMenuShareAppMessage({
			            title: _this.shareCon.payTitle, // 分享标题
			            desc: _this.shareCon.payDesc, // 分享描述
			            link: _this.shareCon.shareUrl, // 分享链接
			            imgUrl: _this.shareCon.payImg, // 分享图标
			            success: function() {
			                // 用户确认分享后执行的回调函数
			                Lib.M.tipsMsg('分享成功')
			            },
			            cancel: function() {
			                // 用户取消分享后执行的回调函数
			                 Lib.M.tipsMsg('取消分享')
			            },
			            error: function(res) {
			                alert(res)
			            }
		            });
		            //分享到朋友圈
		            wx.onMenuShareTimeline({
		             	title: _this.shareCon.payTitle, // 分享标题
		             	desc: _this.shareCon.payDesc, // 分享描述
		              	link: _this.shareCon.shareUrl, // 分享链接
		              	imgUrl: _this.shareCon.payImg, // 分享图标
		              	success: function() {
		                // 用户确认分享后执行的回调函数
		              	},
		              	cancel: function() {
		                // 用户取消分享后执行的回调函数
		              	}
		            });
		        });
			}
		}
	}
</script>
<style scope>

</style>


